          :model="formInline"
          class="demo-form-inline"
          style="margin-top:10px"
        >
          <el-form-item size="mini">
            <el-input v-model="formInline.user" placeholder="名称"></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <el-input v-model="formInline.region" placeholder="类型"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button icon="el-icon-search" @click="onSubmit" size="mini"
              >查询</el-button
