                >
              </div></el-col
            >
          </el-row>
          <el-table
            border
            :data="tableData"
            style="width: 100%;margin-top:10px"
            :default-sort="{ prop: 'date', order: 'descending' }"
          >
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column prop="date" label="名称" sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="类型  " sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="备注" sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="操作" sortable width="150">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index, scope.row)"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
