<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>部门管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体内容 -->
    <div>
      <el-container
        style="height: 500px; border: 1px solid #eee;margin-top: 20px"
      >
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu>
            <el-tree
              :data="data"
              :props="defaultProps"
              @node-click="handleNodeClick"
            ></el-tree>
          </el-menu>
        </el-aside>
  
        <el-container style="margin-top: 5px;margin-left: 5px">
          <el-menu>
            <el-button type="primary" icon="el-icon-circle-close" size="mini"
              >取消</el-button
            >
            <el-button type="primary" icon="el-icon-document" size="mini"
              >保存</el-button
            >

            <el-form
              ref="ruleForm"
              :rules="rules"
              :inline="true"
              :model="ruleForm"
              class="user-search"
              label-width="140px"
            >
              <el-form-item label="部门类型" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="部门维度" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="上级部门名称">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="上级部门编号">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="部门名称" prop="name">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="部门编号" prop="name">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="状态" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="备注">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
            </el-form>
          </el-menu>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [
        {
          label: "一级 1",
          children: [
            {
              label: "二级 1-1",
              children: [
                {
                  label: "三级 1-1-1"
                }
              ]
            }
          ]
        },
        {
          label: "一级 2",
          children: [
            {
              label: "二级 2-1",
              children: [
                {
                  label: "三级 2-1-1"
                }
              ]
            },
            {
              label: "二级 2-2",
              children: [
                {
                  label: "三级 2-2-1"
                }
              ]
            }
          ]
        },
        {
          label: "一级 3",
          children: [
            {
              label: "二级 3-1",
              children: [
                {
                  label: "三级 3-1-1"
                }
              ]
            },
            {
              label: "二级 3-2",
              children: [
                {
                  label: "三级 3-2-1"
                }
              ]
            }
          ]
        }
      ],
      ruleForm: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        region: [
          { required: true, message: "请选择活动区域", trigger: "change" }
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change"
          }
        ],
        resource: [
          { required: true, message: "请选择活动资源", trigger: "change" }
        ],
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      }
    };
  }
};
</script>

<style>
.user-search {
  margin-top: 10px;
}
</style>



